
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: antiquewhite;
  /* border: 1px solid black; */
}

h1{
  /* border: 1px solid black; */
  padding: 15px;
  margin-bottom: 10px;
}

h4{
  /* border: 1px solid black; */
  padding: 20px;
}

.body-container{
  border: 1px solid black;
  margin-top: 10px;
  padding: 20px;
  background-color: aqua;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: grid;
  grid-template-columns: 40px 100px 40px;
}

.dec{
  font-size: 25px;
  font-weight: 600;
  color: blue;
  padding: 10px;
  margin-right: 10px;
  /* border: 1px solid black; */
}
.dec:hover{
  border: 1px solid black;
  border-radius: 10px;
  background-color: aquamarine;
}

.inc{
  font-size: 25px;
  font-weight: 600;
  color: blue;
  padding: 10px 10px 10px 10px;
  margin-left: 10px;
  /* border: 1px solid black; */
}
.inc:hover{
  border: 1px solid black;
  border-radius: 10px;
  background-color: aquamarine;
}

a{
  text-decoration: none;
}
.input_container{
  height: 60px;
  width: 100px;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  color: blue;
  border-radius: 10px;
  border: 1px solid rgb(160, 170, 24);
  background-color: rgba(10, 11, 112,.2);
}